import React from "react";
import { Link, graphql, useStaticQuery } from "gatsby";

import Layout from "../components/layout";
// import Image from "../components/image";
import SEO from "../components/seo";


const IndexPage = () => { 

    const data = useStaticQuery(graphql`
        query {
            ecommerceData {
                internal {
                    content
                }
            }
        }
    `)
    
  const res = data.ecommerceData.internal.content;
        var x = res.split()
  console.log(x);
  return(
    <Layout>
        <SEO title="Home" />
        
        <div>
            {x.map((data, index) => ( 
                <div key={index}>
                    <h1>{data.title}</h1>
                    <h1>{data.title}</h1>
                    <Link to={data.url} />
                </div>
            ))}
        </div>

    </Layout>
  )
};

export default IndexPage;
